* {
  box-sizing: border-box;
}

body{
  font-family: 'Lato';
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}
button {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

ul{
  margin: 0;
  padding: 0;
}
